import React from "react";
import { Grid, Typography, Box } from "@material-ui/core";
import { colors } from "../theme/colors.theme";

const About = () => {
  const { purple, white, blackLight } = colors;

  return (
    <Box component="section" p={3} bgcolor={white} color={blackLight}>
      <Grid container spacing={3} alignItems="center">
        <Grid item xs={12} md={6} lg={6}>
          <Box textAlign="center">
            <Typography variant="h2" style={{ fontWeight: 900, color: purple }}>
              ¿Quién soy?
            </Typography>
          </Box>
        </Grid>
        <Grid item xs={12} md={6} lg={6}>
          <Box mb={1}>
          <Typography component="p" variant="h5" style={{fontWeight:600}}>
            Soy <span className="about-bold">Fernando</span>, un desarollador de
            software que vive en <span className="about-bold">Costa Rica</span>,
            actualmente estoy estudiando ingeniería en sistemas en la{" "}
            <span className="about-bold">UNA</span> y me estoy formando
            profesionalmente en el ámbito de programación en{" "}
            <span className="about-bold">Platzi</span>.
          </Typography>
          </Box>
          <Typography component="p" variant="h5" style={{fontWeight:600}}>
            Tengo 23 años, tengo experiencia en tecnologías tanto de{" "}
            <span className="about-bold">frontend</span> como de{" "}
            <span className="about-bold">backend</span>, para poder desarrollar
            proyectos de calidad.
          </Typography>
        </Grid>
      </Grid>
      <Box height={130} />
    </Box>
  );
};

export default About;
